import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: "/",
      name:'图片上传',
      redirect: 'login'
    },
    {
      path: '/login',
      component:resolve=>require(["@/components/login"],resolve),
      meta: {
        title:'图片上传'
      }
    },
    {
      path: '/Cashier',
      name: 'Cashier',
      component:resolve=>require(["@/components/Cashier"],resolve),
      meta: {
        title:'图片上传'
      }
    }
  ]
})




