import unit from 'axios'
import router from '../router'
//unit.defaults.baseURL="http://"+window.location.host;
unit.defaults.baseURL="http://merchant.inlee.com.cn/v3";
//unit.defaults.baseURL="http://125.69.76.147:7081";
unit.defaults.withCredentials=true;
unit.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
unit.defaults.headers.post['Accept'] = 'application/json;charset=UTF-8';
//添加响应拦截器
unit.interceptors.response.use(response =>{
  var res = response.data;
  if(res.code=="1000"){
    router.replace("loginShop");
  }else if('0000'!=res.code){
    console.log(response.data)
    alert(response.data.msg);
  }
  return response.data.data ;
},error =>  {
  alert("服务器异常");
  return Promise.reject(error.response.data)
});
export default  unit






